// import react
import React from "react";

// import tools and static dta
import { comma } from "../../tools/strings";

// import style sheet
import './ProgramsBudgetCard.css';

function ProgramsBudgetCard(props){
    return (
        <div className='programs-budget-card' onClick={props.onClick} style={{cursor: localStorage.getItem('role') === '' ? 'default' : 'pointer'}}>
            <div className="icon">
                {props.icon}
            </div>
            <div className="name">
                {props.name}
            </div>
            <div className="target">
                <b> </b> : {comma(props.currentValue)} .  
            </div>
            <div className="current-target">
                <b> </b> : {comma(props.currentBalance)} .
            </div>
            {localStorage.getItem('role') !== '' ? <div className='click-edit'>   </div> : <></>}
        </div>
    );
}

export default ProgramsBudgetCard;